*, *::before, *::after {
  box-sizing: border-box; }

body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif; }

button, input {
  font-family: 'Roboto', sans-serif;

  &:active, &:hover, &:focus {
    outline: 0;
    outline-offset: 0; } }

button {

  &:hover {
    cursor: pointer; } }

a {
  text-decoration: none; }

