/* font-family: "MyriadPro"; */
@font-face {
  font-family: "MyriadPro";
  src: url("../fonts/MyriadProRegular/MyriadProRegular.eot");
  src: url("../fonts/MyriadProRegular/MyriadProRegular.eot?#iefix")format("embedded-opentype");
  src: url("../fonts/MyriadProRegular/MyriadProRegular.woff") format("woff");
  src: url("../fonts/MyriadProRegular/MyriadProRegular.ttf") format("truetype");
  src: url("../fonts/MyriadProRegular/MyriadProRegular.otf") format("opentype");
  font-style: normal;
  font-weight: normal; }

/* font-family: "PalatinoLinotype-Roman" */
@font-face {
  font-family: "PalatinoLinotype-Roman";
  src: url("../fonts/PalatinoLinotype/PalatinoLinotype-Roman.ttf") format("truetype");
  font-style: normal;
  font-weight: normal; }

